:root{
  --main-color: hsl(200, 50%, 50%); 
  --bg-color: #f0f0f0;
  --gray-color: #999;
  --text-color: #222;
  --text-alpha-color: #222b;
  --success-color: hsl(145, 50%, 45%);
  --error-color: hsl(0, 60%, 60%);
  --header-height: 60px;
  --border-radius: 2rem
}

html{
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
}
*,*::after,*::before{
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}


a{
  text-decoration: none;
  color: var(--main-color);
  cursor: default;
}





.separator{
  display: grid;
  grid-template-columns: 1fr 1rem 1fr;
  align-items: center;
  color: var(--gray-color);
  /* justify-items: center; */
}
.separator > span{
  justify-self: center;
}
.cursor{
  cursor: default;
}
@media (hover: hover){
  a,button,ion-icon[name="arrow-back-outline"], .cursor{
    cursor: pointer;
  }
}



body{
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: var(--main-color);
}
header{
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: var(--header-height);
  background-color: var(--bg-color);
}

.logo{
  font-size: 2rem;
  font-weight: bold;
}
.logo > span{
  font-size: .9rem;
}
.logo-kev{
  font-family: sans-serif;
  color: var(--main-color);
}



.weather-app{
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 0 0;
  width: 90vw;
  max-width: 400px;
  border-radius: 1rem;
  background-color: var(--bg-color);
  /* overflow: hidden; */
}
.weather-header{
  position: relative;
}
.weather-header > ion-icon{
  position: absolute;
  left: 1rem;
  font-size: 1.5rem;
  transition: .1s ease;
  color: var(--main-color);
}
.weather-title{
  font-size: 1.3rem;
  font-weight: 700;
  text-align: center;
  color: var(--main-color);
}
.weather-options{
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 0 1rem 1rem;
}
.weather-subtitle{
  font-size: 1.8rem;
  font-weight: 700;
  text-align: center;
}
.weather-locationBtn{
  padding: .7rem;
  border-radius: var(--border-radius);
  border: none;
  font-size: 1rem;
  font-family: inherit;
  font-weight: 700;
  color: var(--bg-color);
  background-color: var(--main-color);
}
.input-div{
  position: relative;
}
.input-div input{
  padding: .5rem 1rem;
  width: 100%;
  font-size: 1rem;
  font-family: inherit;
  font-weight: 700;
  text-align: center;
  border-radius: var(--border-radius);
  border: 2px solid var(--main-color);
  outline: none;
  background-color: var(--bg-color);
  /* color: var(--main-color); */
}
.input-div label{
  position: absolute;
  top: .75rem;
  left: 50%;
  padding: 0 .4rem;
  font-size: .85rem;
  font-weight: 400;
  color: var(--main-color);
  background-color: var(--bg-color);
  transform: translate(-50%, 0);
  transition: .5s ease;
  pointer-events: none;
}
.input-div input:focus ~ label,
.input-div input:valid ~ label
{
  top: -.55rem;
  transform: translate(-50%, 0) scale(.9);
  font-weight: 900;
}
.weather-result{
  position: absolute;
  top: -15%;
  left: 0;
  right: 0;
  padding: .5rem 1rem;
  border-radius: var(--border-radius);
  text-align: center;
  font-weight: 700;
  background-color: transparent;
  /* transform: translateY(-1rem); */
}



.weather-data{
  display: flex;
  flex-direction: column;
  gap: .5rem;
  align-items: center;
}
.weather-img{
  width: 150px;
}
.weather-temp{
  font-size: 3.5rem;
  font-family: sans-serif;
}
.weather{
  font-weight: 700;
}
.weather-location{
  display: flex;
  align-items: center;
  gap: .2rem;
  color: var(--text-alpha-color);
}
.weather-info{
  display: flex;
  width: 100%;
}
.weather-info > div{
  display: grid;
  grid-template-columns: 2rem 1fr;
  justify-content: center;
  flex-basis: 50%;
  padding: .5rem;
  border-top: 1px solid var(--gray-color);
}
.weather-info > div:first-child{
  border-right: 1px solid var(--gray-color);
}
.weather-info > div > *:first-child{
  grid-row: span 2;
  align-self: center;
  font-size: 2rem;
  color: var(--main-color);
}
.weather-info > div > *:nth-child(2){
  font-family: sans-serif;
  font-weight: 700;
}
.weather-info > div > *:last-child{
  font-size: .75rem;
}

